/**
 * Vue 3 ÁªÑ‰ª∂Â∫ìÊûÑÂª∫Á≠ñÁï•
 *
 * ‰∏∫ Vue 3 ÁªÑ‰ª∂Â∫ìÊèê‰æõÂÆåÊï¥ÁöÑÊûÑÂª∫Á≠ñÁï•ÔºåÂåÖÊã¨Ôºö
 * - Vue SFC ÂçïÊñá‰ª∂ÁªÑ‰ª∂ÁºñËØë
 * - TypeScript ÊîØÊåÅ
 * - Ê†∑ÂºèÊèêÂèñÂíåÂ§ÑÁêÜ
 * - ÁªÑ‰ª∂Á±ªÂûãÂÆö‰πâÁîüÊàê
 * - Êèí‰ª∂ÂºèÂÆâË£ÖÊîØÊåÅ
 *
 * @author LDesign Team
 * @version 1.0.0
 */

import type { ILibraryStrategy } from '../../types/strategy'
import { LibraryType } from '../../types/library'
import type { BuilderConfig, MultiFormatBuildConfig, CSSBuildStrategy } from '../../types/config'
import type { UnifiedConfig } from '../../types/adapter'
import { shouldMinify } from '../../utils/minify-processor'

/**
 * Vue 3 ÁªÑ‰ª∂Â∫ìÊûÑÂª∫Á≠ñÁï•
 */
export class Vue3Strategy implements ILibraryStrategy {
  readonly name = 'vue3'
  readonly supportedTypes: LibraryType[] = [LibraryType.VUE3]
  readonly priority = 10

  /**
   * Â∫îÁî® Vue 3 Á≠ñÁï•
   */
  async applyStrategy(config: BuilderConfig): Promise<UnifiedConfig> {
    // Â¶ÇÊûúÂêØÁî®‰∫ÜÂ§öÊ†ºÂºèÊûÑÂª∫ÔºåËøîÂõûÂ§ö‰∏™ÊûÑÂª∫ÈÖçÁΩÆ
    if (config.multiFormat) {
      return this.applyMultiFormatStrategy(config)
    }

    // Ëß£ÊûêÂÖ•Âè£ÈÖçÁΩÆ
    const resolvedInput = await this.resolveInputEntries(config)

    const unifiedConfig: UnifiedConfig = {
      input: resolvedInput,
      output: this.buildOutputConfig(config),
      plugins: await this.buildPlugins(config),
      external: this.buildExternals(config),
      treeshake: config.performance?.treeshaking !== false,
      onwarn: this.createWarningHandler()
    }

    return unifiedConfig
  }

  /**
   * Â∫îÁî®Â§öÊ†ºÂºèÊûÑÂª∫Á≠ñÁï•ÔºàTDesign È£éÊ†ºÔºâ
   */
  async applyMultiFormatStrategy(config: BuilderConfig): Promise<UnifiedConfig> {
    const multiFormat = config.multiFormat!
    const cssStrategy = config.cssStrategy || 'multi'

    // Ëß£ÊûêÂÖ•Âè£ÈÖçÁΩÆ
    const resolvedInput = await this.resolveInputEntries(config)

    // ÊûÑÂª∫Â§ö‰∏™ËæìÂá∫ÈÖçÁΩÆ
    const outputs = this.buildMultiFormatOutputs(config, multiFormat)

    const unifiedConfig: UnifiedConfig = {
      input: resolvedInput,
      output: outputs,
      plugins: await this.buildMultiFormatPlugins(config, cssStrategy),
      external: this.buildExternals(config),
      treeshake: config.performance?.treeshaking !== false,
      onwarn: this.createWarningHandler()
    }

    return unifiedConfig
  }

  /**
   * Ê£ÄÊü•Á≠ñÁï•ÊòØÂê¶ÈÄÇÁî®
   */
  isApplicable(config: BuilderConfig): boolean {
    return config.libraryType === LibraryType.VUE3
  }

  /**
   * Ëé∑ÂèñÈªòËÆ§ÈÖçÁΩÆ
   */
  getDefaultConfig(): Partial<BuilderConfig> {
    return {
      libraryType: LibraryType.VUE3,
      output: {
        format: ['esm', 'cjs'],
        sourcemap: true
      },
      vue: {
        version: 3,
        jsx: {
          enabled: true
        },
        template: {
          precompile: true
        }
      },
      typescript: {
        declaration: true,
        declarationDir: 'dist',
        target: 'ES2020',
        module: 'ESNext',
        strict: true
      },
      style: {
        extract: true,
        minimize: true,
        autoprefixer: true
      },
      performance: {
        treeshaking: true,
        minify: true
      },
      external: ['vue']
    }
  }

  /**
   * Ëé∑ÂèñÊé®ËçêÊèí‰ª∂
   */
  getRecommendedPlugins(config: BuilderConfig): any[] {
    const plugins = []

    // Vue SFC Êèí‰ª∂
    plugins.push({
      name: 'rollup-plugin-vue',
      options: this.getVueOptions(config)
    })

    // TypeScript Êèí‰ª∂
    plugins.push({
      name: '@rollup/plugin-typescript',
      options: this.getTypeScriptOptions(config)
    })

    // Node Ëß£ÊûêÊèí‰ª∂
    plugins.push({
      name: '@rollup/plugin-node-resolve',
      options: {
        preferBuiltins: false,
        browser: true
      }
    })

    // CommonJS Êèí‰ª∂
    plugins.push({
      name: '@rollup/plugin-commonjs',
      options: {}
    })

    // Ê†∑ÂºèÂ§ÑÁêÜÊèí‰ª∂
    if (config.style?.extract !== false) {
      plugins.push({
        name: 'rollup-plugin-postcss',
        options: this.getPostCSSOptions(config)
      })
    }

    // DTS Êñá‰ª∂Â§çÂà∂Êèí‰ª∂ÔºàÂ¶ÇÊûúÂ≠òÂú® types ÁõÆÂΩïÔºâ
    if (config.dts !== false) {
      plugins.push(this.createDtsCopyPlugin(config))
    }

    // ‰ª£Á†ÅÂéãÁº©Êèí‰ª∂ÔºàÁîü‰∫ßÊ®°ÂºèÔºâ
    if (shouldMinify(config)) {
      plugins.push({
        name: '@rollup/plugin-terser',
        options: {
          compress: {
            drop_console: true,
            drop_debugger: true
          },
          format: {
            comments: false
          }
        }
      })
    }

    return plugins
  }

  /**
   * È™åËØÅÈÖçÁΩÆ
   */
  validateConfig(config: BuilderConfig): any {
    const errors: string[] = []
    const warnings: string[] = []
    const suggestions: string[] = []

    // Ê£ÄÊü•ÂÖ•Âè£Êñá‰ª∂
    if (!config.input) {
      errors.push('Vue 3 Á≠ñÁï•ÈúÄË¶ÅÊåáÂÆöÂÖ•Âè£Êñá‰ª∂')
    }

    // Ê£ÄÊü• Vue ÁâàÊú¨
    if (config.vue?.version && config.vue.version !== 3) {
      warnings.push('ÂΩìÂâçÁ≠ñÁï•ÈíàÂØπ Vue 3 ‰ºòÂåñÔºåÂª∫ËÆÆ‰ΩøÁî® Vue 3')
    }

    // Ê£ÄÊü•Â§ñÈÉ®‰æùËµñ
    if (Array.isArray(config.external) && !config.external.includes('vue')) {
      suggestions.push('Âª∫ËÆÆÂ∞Ü Vue Ê∑ªÂä†Âà∞Â§ñÈÉ®‰æùËµñ‰∏≠‰ª•ÂáèÂ∞ëÂåÖ‰ΩìÁßØ')
    }

    return {
      valid: errors.length === 0,
      errors,
      warnings,
      suggestions
    }
  }

  /**
   * ÊûÑÂª∫ËæìÂá∫ÈÖçÁΩÆ
   */
  private buildOutputConfig(config: BuilderConfig): any {
    const outputConfig = config.output || {}

    // Â¶ÇÊûú‰ΩøÁî®Ê†ºÂºèÁâπÂÆöÈÖçÁΩÆÔºàoutput.esm, output.cjs, output.umdÔºâÔºåÁõ¥Êé•ËøîÂõû
    if (outputConfig.esm || outputConfig.cjs || outputConfig.umd) {
      const result = { ...outputConfig }

      // ‰∏∫ÊØè‰∏™ËæìÂá∫Ê†ºÂºèÊ∑ªÂä† assetFileNames ÈÖçÁΩÆ
      if (result.esm && typeof result.esm === 'object') {
        result.esm = {
          ...result.esm,
          assetFileNames: '[name].[ext]',
          globals: {
            vue: 'Vue',
            ...result.esm.globals
          }
        }
      }

      if (result.cjs && typeof result.cjs === 'object') {
        result.cjs = {
          ...result.cjs,
          assetFileNames: '[name].[ext]',
          globals: {
            vue: 'Vue',
            ...result.cjs.globals
          }
        }
      }

      if (result.umd && typeof result.umd === 'object') {
        result.umd = {
          ...result.umd,
          assetFileNames: '[name].[ext]',
          globals: {
            vue: 'Vue',
            ...result.umd.globals
          }
        }
      }

      // Á°Æ‰øùÂÖ®Â±ÄÂèòÈáèÂåÖÂê´ Vue
      result.globals = {
        vue: 'Vue',
        ...result.globals
      }

      return result
    }

    // Âê¶Âàô‰ΩøÁî®‰º†ÁªüÁöÑ format Êï∞ÁªÑÈÖçÁΩÆ
    const formats = Array.isArray(outputConfig.format)
      ? outputConfig.format
      : [outputConfig.format || 'esm']

    return {
      dir: outputConfig.dir || 'dist',
      format: formats,
      sourcemap: outputConfig.sourcemap !== false,
      exports: 'named',
      globals: {
        vue: 'Vue',
        ...outputConfig.globals
      }
    }
  }

  /**
   * ÊûÑÂª∫Êèí‰ª∂ÈÖçÁΩÆ
   */
  private async buildPlugins(config: BuilderConfig): Promise<any[]> {
    const plugins: any[] = []

    try {
      // Vue TSX/JSX ÊîØÊåÅÔºàÂøÖÈ°ªÂú® Vue SFC Êèí‰ª∂‰πãÂâçÔºâ
      try {
        const { default: VueJsx } = await import('unplugin-vue-jsx/rollup')
        plugins.push(VueJsx({
          version: 3, // Vue 3
          optimize: config.mode === 'production'
        }))
      } catch (e) {
        // Â¶ÇÊûúÊú™ÂÆâË£Ö JSX Êèí‰ª∂ÔºåÂàôË∑≥ËøáÔºà‰ªçÁÑ∂ÂÖÅËÆ∏Á∫Ø Vue SFC ÊûÑÂª∫Ôºâ
        console.warn('unplugin-vue-jsx Êú™ÂÆâË£ÖÔºåË∑≥Ëøá JSX/TSX ÊîØÊåÅ')
      }

      // Vue SFC Êèí‰ª∂Ôºà‰ΩøÁî® rollup-plugin-vueÔºåÊõ¥Á®≥ÂÆöÔºâ
      const VuePlugin = await import('rollup-plugin-vue')

      // Ê≥®ÂÜå TypeScript ÊîØÊåÅ‰ª•Ëß£ÂÜ≥ "No fs option provided to compileScript" ÈîôËØØ
      try {
        const { registerTS } = await import('@vue/compiler-sfc')
        const typescript = await import('typescript')
        registerTS(() => typescript.default)
      } catch (error) {
        // Â¶ÇÊûúÊó†Ê≥ïÂØºÂÖ• TypeScript Êàñ @vue/compiler-sfcÔºåÁªßÁª≠ÊâßË°å
        console.warn('Failed to register TypeScript support for Vue SFC:', error)
      }

      plugins.push(VuePlugin.default({
        preprocessStyles: true,
        // Âè™Â§ÑÁêÜ .vue Êñá‰ª∂
        include: /\.vue$/,
        ...this.getVueOptions(config)
      }))

      // Node Ëß£ÊûêÊèí‰ª∂
      const nodeResolve = await import('@rollup/plugin-node-resolve')
      plugins.push(nodeResolve.default({
        preferBuiltins: false,
        browser: true,
        extensions: ['.mjs', '.js', '.json', '.ts', '.tsx', '.vue']
      }))

      // CommonJS Êèí‰ª∂
      const commonjs = await import('@rollup/plugin-commonjs')
      plugins.push(commonjs.default())
      // esbuild Êèí‰ª∂Â§ÑÁêÜ TypeScript Âíå JSXÔºà‰øùÁïô JSX ËØ≠Ê≥ïÔºâ
      const { default: esbuild } = await import('rollup-plugin-esbuild')
      plugins.push(esbuild({
        include: /\.(ts|tsx|js|jsx)$/,
        exclude: [/node_modules/],
        target: 'es2020',
        // ‰øùÁïô JSX/TSX ‰ª•‰æøÂêéÁª≠Áî± Vue JSX Êèí‰ª∂Â§ÑÁêÜ
        jsx: 'preserve',
        tsconfig: 'tsconfig.json',
        minify: shouldMinify(config),
        sourceMap: config.output?.sourcemap !== false
      }))

      // JSON Êèí‰ª∂
      const json = await import('@rollup/plugin-json')
      plugins.push(json.default())

      // Ê†∑ÂºèÂ§ÑÁêÜÊèí‰ª∂Ôºà‰ΩøÁî® rollup-plugin-stylesÔºåÊõ¥Â•ΩÁöÑ Vue SFC ÊîØÊåÅÔºâ
      try {
        const Styles = await import('rollup-plugin-styles')
        plugins.push(Styles.default({
          mode: 'extract',
          modules: false,
          minimize: shouldMinify(config),
          namedExports: true,
          include: [
            '**/*.less',
            '**/*.css',
            '**/*.scss',
            '**/*.sass'
          ],
          url: {
            inline: false,
          },
          ...this.getStylesOptions(config)
        }))
      } catch (e) {
        // Â¶ÇÊûú rollup-plugin-styles ‰∏çÂèØÁî®ÔºåÂõûÈÄÄÂà∞ postcss
        const postcss = await import('rollup-plugin-postcss')
        plugins.push(postcss.default({
          ...this.getPostCSSOptions(config),
          include: [
            /\.(css|less|scss|sass)$/,
            /\?vue&type=style/
          ]
        }))
      }
    } catch (error) {
      console.error('Êèí‰ª∂Âä†ËΩΩÂ§±Ë¥•:', error)
    }

    // Ê∑ªÂä† DTS Â§çÂà∂Êèí‰ª∂
    plugins.push(this.createDtsCopyPlugin(config))

    return plugins
  }

  /**
   * ÊûÑÂª∫Â§ñÈÉ®‰æùËµñÈÖçÁΩÆ
   */
  private buildExternals(config: BuilderConfig): string[] | ((id: string) => boolean) {
    let externals: string[] = []

    if (Array.isArray(config.external)) {
      externals = [...config.external]
    } else if (typeof config.external === 'function') {
      // Â¶ÇÊûúÊòØÂáΩÊï∞ÔºåÁõ¥Êé•ËøîÂõû
      return config.external
    } else {
      externals = []
    }

    // Á°Æ‰øù Vue ÊòØÂ§ñÈÉ®‰æùËµñ
    if (!externals.includes('vue')) {
      externals.push('vue')
    }

    // Ê∑ªÂä† node_modules ÊéíÈô§ËßÑÂàô
    return (id: string) => {
      // ÊéíÈô§ node_modules ‰∏≠ÁöÑÊâÄÊúâÊ®°Âùó
      if (id.includes('node_modules')) {
        return true
      }

      // Ê£ÄÊü•ÊòØÂê¶Âú®Â§ñÈÉ®‰æùËµñÂàóË°®‰∏≠
      return externals.some(ext => {
        return id === ext || id.startsWith(ext + '/')
      })
    }
  }

  /**
   * Ëé∑Âèñ Vue ÈÄâÈ°π
   */
  private getVueOptions(config: BuilderConfig): any {
    const vueConfig = config.vue || {}

    return {
      include: /\.vue$/,
      exclude: /node_modules/,
      // Ê®°ÊùøÁºñËØëÈÄâÈ°π
      template: {
        compilerOptions: {
          isCustomElement: (tag: string) => tag.startsWith('ld-') || tag.startsWith('template-'),
          // ÂêØÁî®Áîü‰∫ß‰ºòÂåñ
          hoistStatic: config.mode === 'production',
          cacheHandlers: config.mode === 'production',
        },
        ...vueConfig.template
      },
      // ËÑöÊú¨Â§ÑÁêÜÈÄâÈ°π
      script: {
        // ÂêØÁî® defineModel ÂÆè
        defineModel: true,
        // ÂêØÁî® props Ëß£ÊûÑ
        propsDestructure: true,
        ...vueConfig.script
      },
      // Ê†∑ÂºèÂ§ÑÁêÜÈÄâÈ°π
      style: {
        // ÂêØÁî® CSS Ê®°Âùó
        modules: vueConfig.cssModules !== false,
        ...vueConfig.style
      },
      // ‰º†ÈÄíÂÖ∂‰ªñÁî®Êà∑ÈÖçÁΩÆ
      ...vueConfig
    }
  }

  /**
   * Ëé∑Âèñ TypeScript ÈÄâÈ°π
   */
  private getTypeScriptOptions(config: BuilderConfig): any {
    const tsConfig = config.typescript || {}

    return {
      target: tsConfig.target || 'ES2020',
      module: tsConfig.module || 'ESNext',
      declaration: tsConfig.declaration !== false,
      // declarationDir Â∞ÜÁî± RollupAdapter Âä®ÊÄÅËÆæÁΩÆ
      strict: tsConfig.strict !== false,
      esModuleInterop: true,
      allowSyntheticDefaultImports: true,
      skipLibCheck: true,
      moduleResolution: 'node',
      resolveJsonModule: true,
      // JSX ÈÖçÁΩÆ
      jsx: 'preserve',
      jsxImportSource: 'vue',
      // Vue Áõ∏ÂÖ≥Á±ªÂûã
      types: ['vue', '@vue/runtime-core', ...(tsConfig.types || [])],
      // Ë∑ØÂæÑÊò†Â∞Ñ
      paths: {
        '@/*': ['src/*'],
        ...tsConfig.paths
      },
      ...tsConfig
    }
  }

  /**
   * Ëé∑Âèñ PostCSS ÈÄâÈ°π
   */
  private getPostCSSOptions(config: BuilderConfig): any {
    return {
      extract: config.style?.extract !== false,
      minimize: config.style?.minimize !== false,
      sourceMap: config.output?.sourcemap !== false,
      modules: config.style?.modules || false,
      // ÊîØÊåÅ less/scss Á≠âÈ¢ÑÂ§ÑÁêÜÂô®
      use: ['less'],
      extensions: ['.css', '.less', '.scss', '.sass']
    }
  }

  /**
   * Ëé∑Âèñ rollup-plugin-styles ÈÄâÈ°π
   */
  private getStylesOptions(config: BuilderConfig): any {
    return {
      // Ê†∑ÂºèÊèêÂèñÈÖçÁΩÆ
      extract: config.style?.extract !== false,
      minimize: shouldMinify(config),
      sourceMap: config.output?.sourcemap !== false,
      modules: config.style?.modules || false,
      // ÊîØÊåÅÁöÑÊñá‰ª∂Êâ©Â±ïÂêç
      extensions: ['.css', '.less', '.scss', '.sass'],
      // È¢ÑÂ§ÑÁêÜÂô®ÈÖçÁΩÆ
      less: {
        javascriptEnabled: true
      },
      scss: {
        includePaths: ['node_modules']
      }
    }
  }

  /**
   * ÂàõÂª∫ DTS Êñá‰ª∂ÁîüÊàêÊèí‰ª∂
   */
  private createDtsCopyPlugin(config?: BuilderConfig): any {
    return {
      name: 'generate-dts-files',
      writeBundle: async (options: any) => {
        // Ê£ÄÊü•ÊòØÂê¶‰∏∫ silent Ê®°ÂºèÔºåÂ¶ÇÊûúÊòØÂàô‰∏çËæìÂá∫Ë∞ÉËØï‰ø°ÊÅØ
        const isSilent = config?.logLevel === 'silent'

        if (!isSilent) {
          console.log('üîç DTS Êèí‰ª∂Ë¢´Ë∞ÉÁî®Ôºåoptions:', options)
        }

        try {
          const outputDir = options.dir
          if (!outputDir) {
            if (!isSilent) {
              console.log('‚ö†Ô∏è ËæìÂá∫ÁõÆÂΩï‰∏∫Á©∫')
            }
            return
          }

          if (!isSilent) {
            console.log('üîß ÂºÄÂßãÁîüÊàê TypeScript Â£∞ÊòéÊñá‰ª∂')
          }
          await this.generateDtsFiles(outputDir, config)

        } catch (error) {
          if (!isSilent) {
            console.warn('‚ö†Ô∏è Â§ÑÁêÜ DTS Êñá‰ª∂Â§±Ë¥•:', error instanceof Error ? error.message : String(error))
          }
        }
      }
    }
  }

  /**
   * ‰ΩøÁî® TypeScript ÁºñËØëÂô®ÁîüÊàê DTS Êñá‰ª∂
   */
  private async generateDtsFiles(outputDir: string, config?: BuilderConfig): Promise<void> {
    const isSilent = config?.logLevel === 'silent'

    try {
      const fs = await import('fs')
      const path = await import('path')

      // Â∞ùËØïÂØºÂÖ• TypeScript
      let ts: any
      try {
        ts = await import('typescript')
      } catch (error) {
        if (!isSilent) {
          console.warn('‚ö†Ô∏è Êó†Ê≥ïÂØºÂÖ• TypeScriptÔºåË∑≥Ëøá DTS ÁîüÊàê')
        }
        return
      }

      const rootDir = process.cwd()
      const srcDir = path.join(rootDir, 'src')
      const tsconfigPath = path.join(rootDir, 'tsconfig.json')

      // Ê£ÄÊü• src ÁõÆÂΩïÂíå tsconfig.json ÊòØÂê¶Â≠òÂú®
      if (!fs.existsSync(srcDir)) {
        if (!isSilent) {
          console.log('‚ö†Ô∏è src ÁõÆÂΩï‰∏çÂ≠òÂú®ÔºåË∑≥Ëøá DTS ÁîüÊàê')
        }
        return
      }

      // ËØªÂèñÂíåËß£Êûê tsconfig.json
      let parsedConfig: any
      if (fs.existsSync(tsconfigPath)) {
        const tsconfigContent = fs.readFileSync(tsconfigPath, 'utf-8')
        const configFile = ts.parseConfigFileTextToJson(tsconfigPath, tsconfigContent)

        if (configFile.error) {
          if (!isSilent) {
            console.warn('‚ö†Ô∏è Ëß£Êûê tsconfig.json Â§±Ë¥•:', configFile.error.messageText)
          }
          parsedConfig = { compilerOptions: {} }
        } else {
          parsedConfig = ts.parseJsonConfigFileContent(
            configFile.config,
            ts.sys,
            path.dirname(tsconfigPath)
          )
        }
      } else {
        if (!isSilent) {
          console.log('‚ö†Ô∏è tsconfig.json ‰∏çÂ≠òÂú®Ôºå‰ΩøÁî®ÈªòËÆ§ÈÖçÁΩÆ')
        }
        parsedConfig = {
          options: {},
          fileNames: [],
          errors: []
        }
      }

      // Ëé∑ÂèñÊâÄÊúâ TypeScript Êñá‰ª∂
      const glob = await import('glob')
      const tsFiles = await glob.glob('**/*.{ts,tsx}', {
        cwd: srcDir,
        absolute: true,
        ignore: ['**/*.test.ts', '**/*.spec.ts', '**/__tests__/**', '**/*.d.ts']
      })

      if (tsFiles.length === 0) {
        if (!isSilent) {
          console.log('‚ö†Ô∏è Ê≤°ÊúâÊâæÂà∞ TypeScript Êñá‰ª∂')
        }
        return
      }

      if (!isSilent) {
        console.log(`üîß ÂºÄÂßãÁîüÊàê ${tsFiles.length} ‰∏™Êñá‰ª∂ÁöÑ TypeScript Â£∞ÊòéÊñá‰ª∂`)
      }

      // ÂàõÂª∫ÁºñËØëÈÄâÈ°π
      const compilerOptions: any = {
        ...parsedConfig.options,
        declaration: true,
        emitDeclarationOnly: true,
        outDir: outputDir,
        rootDir: srcDir,
        skipLibCheck: true,
        moduleResolution: ts.ModuleResolutionKind.NodeJs,
        target: ts.ScriptTarget.ES2020,
        module: ts.ModuleKind.ESNext,
        jsx: ts.JsxEmit.ReactJSX,
        allowSyntheticDefaultImports: true,
        esModuleInterop: true,
        strict: false,
        noEmitOnError: false
      }

      // ÂàõÂª∫ÁºñËØëÂô®‰∏ªÊú∫
      const host = ts.createCompilerHost(compilerOptions)

      // ÂàõÂª∫ TypeScript Á®ãÂ∫è
      const program = ts.createProgram(tsFiles, compilerOptions, host)

      // ÁîüÊàêÂ£∞ÊòéÊñá‰ª∂
      const emitResult = program.emit(undefined, undefined, undefined, true)

      // Ê£ÄÊü•ÁºñËØëÈîôËØØ
      const allDiagnostics = ts.getPreEmitDiagnostics(program).concat(emitResult.diagnostics)

      if (allDiagnostics.length > 0 && !isSilent) {
        console.warn('‚ö†Ô∏è TypeScript ÁºñËØëË≠¶Âëä:')
        allDiagnostics.forEach((diagnostic: any) => {
          if (diagnostic.file) {
            const { line, character } = diagnostic.file.getLineAndCharacterOfPosition(diagnostic.start!)
            const message = ts.flattenDiagnosticMessageText(diagnostic.messageText, '\n')
            console.warn(`  ${path.relative(rootDir, diagnostic.file.fileName)} (${line + 1},${character + 1}): ${message}`)
          } else {
            console.warn(`  ${ts.flattenDiagnosticMessageText(diagnostic.messageText, '\n')}`)
          }
        })
      }

      if (emitResult.emitSkipped) {
        if (!isSilent) {
          console.warn('‚ö†Ô∏è TypeScript Â£∞ÊòéÊñá‰ª∂ÁîüÊàêÂ§±Ë¥•')
        }
      } else {
        // ÁªüËÆ°ÁîüÊàêÁöÑ .d.ts Êñá‰ª∂Êï∞Èáè
        const generatedDtsFiles = await glob.glob('**/*.d.ts', {
          cwd: outputDir,
          absolute: false
        })
        if (!isSilent) {
          console.log(`‚úÖ TypeScript Â£∞ÊòéÊñá‰ª∂ÁîüÊàêÊàêÂäüÔºåÂÖ±ÁîüÊàê ${generatedDtsFiles.length} ‰∏™ .d.ts Êñá‰ª∂`)
        }
      }

    } catch (error) {
      if (!isSilent) {
        console.warn('‚ö†Ô∏è ÁîüÊàê TypeScript Â£∞ÊòéÊñá‰ª∂Â§±Ë¥•:', error instanceof Error ? error.message : String(error))
      }
    }
  }

  /**
   * ÂàõÂª∫Ë≠¶ÂëäÂ§ÑÁêÜÂô®
   */
  private createWarningHandler() {
    return (warning: any) => {
      // ÂøΩÁï•‰∏Ä‰∫õÂ∏∏ËßÅÁöÑÊó†ÂÆ≥Ë≠¶Âëä
      if (warning.code === 'THIS_IS_UNDEFINED') {
        return
      }

      if (warning.code === 'CIRCULAR_DEPENDENCY') {
        return
      }

      console.warn(`Warning: ${warning.message}`)
    }
  }

  /**
   * Ëß£ÊûêÂÖ•Âè£ÈÖçÁΩÆ
   * - Ëã•Áî®Êà∑Êú™‰º†ÂÖ• inputÔºåÂàôÂ∞Ü src ‰∏ãÊâÄÊúâÊ∫êÊñá‰ª∂‰Ωú‰∏∫ÂÖ•Âè£ÔºàÊéíÈô§ÊµãËØï‰∏éÂ£∞ÊòéÊñá‰ª∂Ôºâ
   * - Ëã•Áî®Êà∑‰º†ÂÖ• glob Ê®°ÂºèÁöÑÊï∞ÁªÑÔºåÂàôËß£Êûê‰∏∫Â§öÂÖ•Âè£
   * - Ëã•Áî®Êà∑‰º†ÂÖ•Âçï‰∏™Êñá‰ª∂ÊàñÂØπË±°ÔºåÂàôÁõ¥Êé•ËøîÂõû
   */
  private async resolveInputEntries(config: BuilderConfig): Promise<string | string[] | Record<string, string>> {
    // Â¶ÇÊûúÊ≤°ÊúâÊèê‰æõinputÔºåËá™Âä®Êâ´ÊèèsrcÁõÆÂΩï
    if (!config.input) {
      return this.autoDiscoverEntries()
    }

    // Â¶ÇÊûúÊòØÂ≠óÁ¨¶‰∏≤Êï∞ÁªÑ‰∏îÂåÖÂê´globÊ®°ÂºèÔºåËß£Êûê‰∏∫Â§öÂÖ•Âè£
    if (Array.isArray(config.input)) {
      return this.resolveGlobEntries(config.input)
    }

    // ÂÖ∂‰ªñÊÉÖÂÜµÁõ¥Êé•ËøîÂõûÁî®Êà∑ÈÖçÁΩÆ
    return config.input
  }

  /**
   * Ëá™Âä®ÂèëÁé∞ÂÖ•Âè£Êñá‰ª∂
   */
  private async autoDiscoverEntries(): Promise<string | Record<string, string>> {
    const { findFiles } = await import('../../utils/file-system')
    const { relative, extname } = await import('path')

    const files = await findFiles([
      'src/**/*.{ts,tsx,js,jsx,vue,json}'
    ], {
      cwd: process.cwd(),
      ignore: ['**/*.d.ts', '**/*.test.*', '**/*.spec.*', '**/__tests__/**']
    })

    if (files.length === 0) return 'src/index.ts'

    const entryMap: Record<string, string> = {}
    for (const abs of files) {
      const rel = relative(process.cwd(), abs)
      const relFromSrc = rel.replace(/^src[\\/]/, '')
      const noExt = relFromSrc.slice(0, relFromSrc.length - extname(relFromSrc).length)
      const key = noExt.replace(/\\/g, '/')
      entryMap[key] = abs
    }
    return entryMap
  }

  /**
   * Ëß£ÊûêglobÊ®°ÂºèÁöÑÂÖ•Âè£ÈÖçÁΩÆ
   */
  private async resolveGlobEntries(patterns: string[]): Promise<Record<string, string>> {
    const { findFiles } = await import('../../utils/file-system')
    const { relative, extname } = await import('path')

    const files = await findFiles(patterns, {
      cwd: process.cwd(),
      ignore: ['**/*.d.ts', '**/*.test.*', '**/*.spec.*', '**/__tests__/**']
    })

    if (files.length === 0) {
      throw new Error(`No files found matching patterns: ${patterns.join(', ')}`)
    }

    const entryMap: Record<string, string> = {}
    for (const abs of files) {
      const rel = relative(process.cwd(), abs)
      const relFromSrc = rel.replace(/^src[\\/]/, '')
      const noExt = relFromSrc.slice(0, relFromSrc.length - extname(relFromSrc).length)
      const key = noExt.replace(/\\/g, '/')
      entryMap[key] = abs
    }
    return entryMap
  }

  /**
   * ÊûÑÂª∫Â§öÊ†ºÂºèËæìÂá∫ÈÖçÁΩÆÔºàTDesign È£éÊ†ºÔºâ
   */
  private buildMultiFormatOutputs(config: BuilderConfig, multiFormat: MultiFormatBuildConfig): any[] {
    const outputs: any[] = []
    const baseOutput = config.output || {}

    // ES Ê®°ÂùóÊûÑÂª∫ÔºàÊîØÊåÅ tree-shakingÔºâ
    if (multiFormat.es !== false) {
      outputs.push({
        ...baseOutput,
        dir: 'es/',
        format: 'esm',
        sourcemap: config.sourcemap !== false,
        entryFileNames: '[name].mjs',
        chunkFileNames: '_chunks/dep-[hash].mjs',
        assetFileNames: '[name].[ext]'
      })
    }

    // ESM ÊûÑÂª∫Ôºà‰øùÁïôÊ∫êÁ†ÅÊ†∑ÂºèÂºïÁî®Ôºâ
    if (multiFormat.esm) {
      outputs.push({
        ...baseOutput,
        dir: 'esm/',
        format: 'esm',
        sourcemap: config.sourcemap !== false,
        chunkFileNames: '_chunks/dep-[hash].js'
      })
    }

    // Lib ÊûÑÂª∫ÔºàÂ∫ìÊ†ºÂºèÔºâ
    if (multiFormat.lib) {
      outputs.push({
        ...baseOutput,
        dir: 'lib/',
        format: 'esm',
        sourcemap: config.sourcemap !== false,
        chunkFileNames: '_chunks/dep-[hash].js'
      })
    }

    // CJS ÊûÑÂª∫ÔºàCommonJS Ê†ºÂºèÔºâ
    if (multiFormat.cjs) {
      outputs.push({
        ...baseOutput,
        dir: 'cjs/',
        format: 'cjs',
        sourcemap: config.sourcemap !== false,
        exports: 'named',
        chunkFileNames: '_chunks/dep-[hash].js'
      })
    }

    // UMD ÊûÑÂª∫ÔºàÊµèËßàÂô®Ê†ºÂºèÔºâ
    if (multiFormat.umd) {
      const umdName = config.umd?.name || 'LDesign'
      outputs.push({
        ...baseOutput,
        file: `dist/${config.umd?.filename || 'index'}.js`,
        format: 'umd',
        name: umdName,
        exports: 'named',
        globals: { vue: 'Vue', ...config.globals },
        sourcemap: config.sourcemap !== false
      })
    }

    // UMD ÂéãÁº©ÁâàÊú¨
    if (multiFormat.umdMin) {
      const umdName = config.umd?.name || 'LDesign'
      outputs.push({
        ...baseOutput,
        file: `dist/${config.umd?.filename || 'index'}.min.js`,
        format: 'umd',
        name: umdName,
        exports: 'named',
        globals: { vue: 'Vue', ...config.globals },
        sourcemap: config.sourcemap !== false
      })
    }

    return outputs
  }

  /**
   * ÊûÑÂª∫Â§öÊ†ºÂºèÊèí‰ª∂ÈÖçÁΩÆÔºàTDesign È£éÊ†ºÔºâ
   */
  private async buildMultiFormatPlugins(config: BuilderConfig, cssStrategy: CSSBuildStrategy): Promise<any[]> {
    const plugins: any[] = []

    try {
      // Vue SFC Êèí‰ª∂
      const vuePlugin = await import('rollup-plugin-vue')
      plugins.push(vuePlugin.default({
        target: 'browser',
        css: false, // CSS Áî±‰∏ìÈó®ÁöÑÊèí‰ª∂Â§ÑÁêÜ
        compileTemplate: true,
        preprocessStyles: true
      }))

      // Node Ëß£ÊûêÊèí‰ª∂
      const nodeResolve = await import('@rollup/plugin-node-resolve')
      plugins.push(nodeResolve.default({
        preferBuiltins: false,
        browser: true,
        extensions: ['.mjs', '.js', '.json', '.ts', '.tsx', '.vue']
      }))

      // CommonJS Êèí‰ª∂
      const commonjs = await import('@rollup/plugin-commonjs')
      plugins.push(commonjs.default())

      // esbuild Êèí‰ª∂Â§ÑÁêÜ TypeScript
      const { default: esbuild } = await import('rollup-plugin-esbuild')
      plugins.push(esbuild({
        include: /\.(ts|tsx|js|jsx)$/,
        exclude: [/node_modules/],
        target: 'es2020',
        jsx: 'preserve',
        tsconfig: 'tsconfig.json',
        minify: shouldMinify(config),
        sourceMap: config.sourcemap !== false
      }))

      // JSON Êèí‰ª∂
      const json = await import('@rollup/plugin-json')
      plugins.push(json.default())

      // Ê†πÊçÆ CSS Á≠ñÁï•Ê∑ªÂä†Ê†∑ÂºèÂ§ÑÁêÜÊèí‰ª∂
      plugins.push(...await this.buildCSSStrategyPlugins(config, cssStrategy))

      // Ê∑ªÂä†Â§öÂÖ•Âè£ÊîØÊåÅ
      if (config.multiFormat?.es || config.multiFormat?.esm || config.multiFormat?.lib) {
        try {
          const multiInput = await import('rollup-plugin-multi-input')
          plugins.push(multiInput.default({
            relative: 'src/',
            transformOutputPath: (output: string) => output
          }))
        } catch (e) {
          console.warn('rollup-plugin-multi-input ‰∏çÂèØÁî®ÔºåË∑≥ËøáÂ§öÂÖ•Âè£ÊîØÊåÅ')
        }
      }

    } catch (error) {
      console.error('Êèí‰ª∂Âä†ËΩΩÂ§±Ë¥•:', error)
    }

    return plugins
  }

  /**
   * ÊûÑÂª∫ CSS Á≠ñÁï•Êèí‰ª∂ÔºàTDesign È£éÊ†ºÔºâ
   */
  private async buildCSSStrategyPlugins(config: BuilderConfig, cssStrategy: CSSBuildStrategy): Promise<any[]> {
    const plugins: any[] = []

    switch (cssStrategy) {
      case 'single':
        // CSS ÊâìÂåÖÂà∞‰∏Ä‰∏™Êñá‰ª∂‰∏≠
        try {
          const postcss = await import('rollup-plugin-postcss')
          plugins.push(postcss.default({
            extract: `${shouldMinify(config) ? 'index.min' : 'index'}.css`,
            minimize: shouldMinify(config),
            sourceMap: config.sourcemap !== false,
            extensions: ['.sass', '.scss', '.css', '.less']
          }))
        } catch (e) {
          console.warn('rollup-plugin-postcss ‰∏çÂèØÁî®ÔºåË∑≥Ëøá CSS ÂçïÊñá‰ª∂ÊâìÂåÖ')
        }
        break

      case 'multi':
        // CSS ÂàÜÂà´ÊâìÂåÖÂà∞ÂêÑËá™ÁöÑ style ‰∏ã
        try {
          // ‰ΩøÁî® staticImport Â§ÑÁêÜÊ†∑ÂºèÊñá‰ª∂ÁöÑÈùôÊÄÅÂØºÂÖ•
          const staticImport = await import('rollup-plugin-static-import')
          plugins.push(staticImport.default({
            baseDir: 'src/',
            include: ['**/style/css.mjs']
          }))

          // ‰ΩøÁî® ignoreImport ÂøΩÁï•Ê†∑ÂºèÂØºÂÖ•Ôºå‰ΩÜÊ∑ªÂä†ÊõøÊç¢ÂØºÂÖ•
          const ignoreImport = await import('rollup-plugin-ignore-import')
          plugins.push(ignoreImport.default({
            include: ['**/style/*'],
            body: 'import "./style/css.mjs";'
          }))

          // Â§çÂà∂Ê†∑ÂºèÊñá‰ª∂
          const copy = await import('rollup-plugin-copy')
          plugins.push(copy.default({
            targets: [
              {
                src: 'src/**/style/css.js',
                dest: 'es',
                rename: (name: string, extension: string, fullPath: string) => {
                  return `${fullPath.replace('src/', '').slice(0, -6)}${name}.mjs`
                }
              }
            ],
            verbose: true
          }))
        } catch (e) {
          console.warn('CSS multi Á≠ñÁï•Êèí‰ª∂‰∏çÂèØÁî®ÔºåÂõûÈÄÄÂà∞Âü∫Á°ÄÊ†∑ÂºèÂ§ÑÁêÜ')
          // ÂõûÈÄÄÂà∞Âü∫Á°ÄÊ†∑ÂºèÂ§ÑÁêÜ
          const styles = await import('rollup-plugin-styles')
          plugins.push(styles.default({
            mode: 'extract',
            minimize: shouldMinify(config)
          }))
        }
        break

      case 'source':
        // ‰∏çÊâìÂåÖ lessÔºå‰ΩÜ‰øùÊåÅÂºïÁî®
        // ËøôÁßçÊÉÖÂÜµ‰∏ã‰∏çÊ∑ªÂä†Ê†∑ÂºèÂ§ÑÁêÜÊèí‰ª∂Ôºå‰øùÊåÅÊ∫êÁ†ÅÂºïÁî®
        break

      case 'ignore':
        // ÂÆåÂÖ®ÂøΩÁï• less
        try {
          const ignoreImport = await import('rollup-plugin-ignore-import')
          plugins.push(ignoreImport.default({
            include: ['**/style/index.js', '**/*.less', '**/*.css', '**/*.scss', '**/*.sass']
          }))
        } catch (e) {
          console.warn('rollup-plugin-ignore-import ‰∏çÂèØÁî®ÔºåË∑≥ËøáÊ†∑ÂºèÂøΩÁï•')
        }
        break

      default:
        // ÈªòËÆ§‰ΩøÁî®Âü∫Á°ÄÊ†∑ÂºèÂ§ÑÁêÜ
        try {
          const styles = await import('rollup-plugin-styles')
          plugins.push(styles.default({
            mode: 'extract',
            minimize: shouldMinify(config),
            sourceMap: config.sourcemap !== false
          }))
        } catch (e) {
          console.warn('rollup-plugin-styles ‰∏çÂèØÁî®ÔºåË∑≥ËøáÊ†∑ÂºèÂ§ÑÁêÜ')
        }
    }

    return plugins
  }
}
